.Rtable {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 3em 0;
  padding: 0;
}
.Rtable-cell {
  box-sizing: border-box;
  flex-grow: 1;
  width: 100%;
  padding: 0.8em 1.2em;
  overflow: hidden;
  list-style: none;
  border: solid 2px white;
  background: rgba(112, 128, 144, 0.1);

  ol {
    padding: .8em 1.2em;

    li {
      margin-bottom: .32em;
    }
  }

}

.Rtable-cell--head {
  background: #E82308;
  color: rgba(255,255,255,.9);  
}

.Rtable-cell > h1,
.Rtable-cell > h2,
.Rtable-cell > h3,
.Rtable-cell > h4,
.Rtable-cell > h5,
.Rtable-cell > h6 {
  margin: 0;
}
/* Table column sizing
================================== */
.Rtable--2cols > .Rtable-cell {
  width: 50%;
}
.Rtable--3cols > .Rtable-cell {
  width: 33.33%;
}
.Rtable--4cols > .Rtable-cell {
  width: 25%;
}
.Rtable--5cols > .Rtable-cell {
  width: 20%;
}
.Rtable--6cols > .Rtable-cell {
  width: 16.6%;
}

/* Responsive
==================================== */
@media all and (max-width: 768px) {
  .Rtable--collapse {
    display: block;
  }
  .Rtable--collapse > .Rtable-cell {
    width: 100% !important;
  }
  .Rtable--collapse > .Rtable-cell--foot {
    margin-bottom: 1em;
  }
}
.no-flexbox .Rtable {
  display: block;
}
.no-flexbox .Rtable > .Rtable-cell {
  width: 100%;
}
.no-flexbox .Rtable > .Rtable-cell--foot {
  margin-bottom: 1em;
}


.Rtable-cell.Rtable-cell--1of1 {
  width: 100%;
}
.Rtable-cell.Rtable-cell--5of6 {
  width: 83.33;
}
.Rtable-cell.Rtable-cell--4of5 {
  width: 80%;
}
.Rtable-cell.Rtable-cell--3of4 {
  width: 75%;
}
.Rtable-cell.Rtable-cell--2of3 {
  width: 66.66%;
}
.Rtable-cell.Rtable-cell--3of5 {
  width: 60%;
}
.Rtable-cell.Rtable-cell--1of2 {
  width: 50%;
}
.Rtable-cell.Rtable-cell--2of4 {
  width: 40%;
}
.Rtable-cell.Rtable-cell--1of3 {
  width: 33.33%;
}
.Rtable-cell.Rtable-cell--1of4 {
  width: 25%;
}
.Rtable-cell.Rtable-cell--1of5 {
  width: 20%;
}
.Rtable-cell.Rtable-cell--1of6 {
  width: 16.66%;
}
@media all and (max-width: 500px) {
  .Rtable-cell--rowEnd {
    margin-bottom: 1em;
  }
}
@media all and (min-width: 500px) {
  .hiddenLarge {
    display: none;
  }
}